.section-wrapper {
    position: relative;
}
.gallery {
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translate(-50%, -25%);
    @media screen and (min-width: $tablet) {
        transform: translate(-50%, -44%);
    }
    @media screen and (min-width: $desktop) {
        transform: translate(-50%, -23%);
    }
    width: 280px;
    height: 116px;
    @media screen and (min-width: $tablet) {
        width: 704px;
        height: 327px;
    }
    @media screen and (min-width: $desktop) {
        width: 1013px;
        height: 422px;
    }
    .gallery-slider {
        overflow: visible;
        ul {
        position: relative;
    }
        li {
        position: absolute;
        top: -325px;
    }
}
}
.gallery-content {
    &__photos{
    display: block;
    border-radius: 8px;
    @media screen and (min-width: $tablet) {
        width: 704px;
        height: 327px;
        border-radius: 18px;
    }
    @media screen and (min-width: $desktop) {
        width: 1013px;
        height: 422px;
        border-radius: 24px;
    }
    }
}
.mask {
    overflow: hidden;
    height: 116px;
        @media screen and (min-width: $tablet) {
            height: 327px;
        }
        @media screen and (min-width: $desktop) {
            height: 422px;
        }
}
.gallery-slider {
    .firstanimation {
        animation: cycle 25s linear infinite;
    }
    .secondanimation {
        animation: cycletwo 25s linear infinite;
    }
    .thirdanimation {
        animation: cyclethree 25s linear infinite;
    }
    .fourthanimation {
        animation: cyclefour 25s linear infinite;
    }
    .fifthanimation {
        animation: cyclefive 25s linear infinite;
    }
}
@keyframes cycle {
    0%  { top: 0px; } /*   ,     */
    4%  { top: 0px; } /*   */
    16% { top: 0px; opacity:1; z-index:0; } /*  4%  16 % (3 )   */
    20% { top: 325px; opacity: 0; z-index: 0; } /*  16%  20% (1 ) */
    21% { top: -325px; opacity: 0; z-index: -1; } /*        */
    92% { top: -325px; opacity: 0; z-index: 0; }
    96% { top: -325px; opacity: 0; } /*  96%  100% (1 )   */
    100%{ top: 0px; opacity: 1; }
}
@keyframes cycletwo {
    0%  { top: -325px; opacity: 0; } /*      */
    16% { top: -325px; opacity: 0; }/*    16% */
    20% { top: 0px; opacity: 1; }
    24% { top: 0px; opacity: 1; }  /*  20%  24% (  1 )  */
    36% { top: 0px; opacity: 1; z-index: 0; }   /*  24%  36 % (3 )   */
    40% { top: 325px; opacity: 0; z-index: 0; } /*  36%  40% (1 )  */
    41% { top: -325px; opacity: 0; z-index: -1; }   /*     */
    100%{ top: -325px; opacity: 0; z-index: -1; }
}
@keyframes cyclethree {
    0%  { top: -325px; opacity: 0; }
    36% { top: -325px; opacity: 0; }
    40% { top: 0px; opacity: 1; }
    44% { top: 0px; opacity: 1; }
    56% { top: 0px; opacity: 1; }
    60% { top: 325px; opacity: 0; z-index: 0; }
    61% { top: -325px; opacity: 0; z-index: -1; }
    100%{ top: -325px; opacity: 0; z-index: -1; }
}
@keyframes cyclefour {
    0%  { top: -325px; opacity: 0; }
    56% { top: -325px; opacity: 0; }
    60% { top: 0px; opacity: 1; }
    64% { top: 0px; opacity: 1; }
    76% { top: 0px; opacity: 1; z-index: 0; }
    80% { top: 325px; opacity: 0; z-index: 0; }
    81% { top: -325px; opacity: 0; z-index: -1; }
    100%{ top: -325px; opacity: 0; z-index: -1; }
}
@keyframes cyclefive {
    0%  { top: -325px; opacity: 0; }
    76% { top: -325px; opacity: 0; }
    80% { top: 0px; opacity: 1; }
    84% { top: 0px; opacity: 1; }
    96% { top: 0px; opacity: 1; z-index: 0; }
    100%{ top: 325px; opacity: 0; z-index: 0; }
}