.backdrop {

    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 3;
    opacity: 1;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }
    
.is-hidden {
    .modal-buy {
        transform: translate(-50%, -50%) scale(0.8);
    }
        
opacity: 0;
pointer-events: none;
}
    

    .close__icon--black {
        width: 20px;
        height: 20px;
        @media screen and (min-width:768px) {
        width: 24px;
        height: 24px;
        }
    }


    .modal-buy {
        width: 280px;
        height: fit-content;
        padding: 28px;
        background-color: #fff;
        border-radius: 10px;
        border: 2px solid #F8D24E;
        max-height: 90%;
        overflow-y: scroll;
      
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) scale(1);
        transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
        
        @media screen and (min-width:768px) {
        width: 568px;
          
          padding: 32px 54px;
          }


          &__title {
            font-family: $secondary-font;
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: calc(24 / 16);
            display: flex;
            align-items: center;
            text-align: center;
            justify-content: center;
            color: #131313;
    
            margin-bottom: 20px;
    
            @media screen and (min-width:768px) {
                margin-bottom: 40px;
            }
    
        }
    }    

    .modal-buy__button-send{
        min-width: 132px;
        padding: 12px 34px;
        margin: 0 auto;
        
    
        border: none;
    
        background: $yellow-bt-cl;
        border-radius: 10px;
    
        font-family: $main-font;
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 1.21;
        display: block;
        align-items: center;
        text-align: center;
        transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1),
                    background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    
        color: #131313;
        cursor: pointer;

        &:hover,
        &:focus {
        color: $white-txt-cl;
        background-color: $green-bt-cl;
        }
      }

    .modal-buy__photo-container {
        position: relative;
        }

    .modal-buy__photo-container::after {
        content: "";
        position: absolute;
        width: 132px;
        height: 132px;
        border-radius: 50%;
        top: 7px;
        left: 50%;
        transform: translateX(-50%);
        z-index: -1;
    }

    .modal-buy__photo-container--first::after {
        background: linear-gradient(0deg, rgba(156, 190, 143, 0.7), rgba(156, 190, 143, 0.7)), #D9D9D9;
        box-shadow: 0px 5.26027px 19.726px rgba(10, 136, 23, 0.25);
    }

    .modal-buy__photo-container--second::after {
        background: linear-gradient(0deg, rgba(248, 210, 78, 0.7), rgba(248, 210, 78, 0.7)), #D9D9D9;
        box-shadow: 0px 5.26027px 19.726px rgba(153, 114, 23, 0.25);
    }

    .modal-buy__photo-container--third::after {
        background: linear-gradient(0deg, rgba(213, 117, 63, 0.7), rgba(213, 117, 63, 0.7)), #D9D9D9;
        box-shadow: 0px 5.26027px 19.726px rgba(0, 0, 0, 0.25);
    }

    .modal-buy__photo {
        display: block;
        width: 100%;
        height: 100%;
    
    // ANIMATION FOR IMG
        transform: scale(1);
        transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1),
                    border 250ms cubic-bezier(0.4, 0, 0.2, 1);
    
        .ice-cream-types__item:hover &,
        .ice-cream-types__item:focus & {
            transform: scale(1.1);
                }
    }

     
    .ice-cream-types__ckeckbox--first:checked ~ .modal-buy__photo-container--first>.modal-buy__photo  {
        transform: scale(1.2);
        border: solid 2px #96AF8C;
        border-radius: 50%;
    }

    .ice-cream-types__ckeckbox--second:checked ~ .modal-buy__photo-container--second>.modal-buy__photo  {
        transform: scale(1.2);
        border: solid 2px #EFD478;
        border-radius: 50%;
    }

    .ice-cream-types__ckeckbox--third:checked ~ .modal-buy__photo-container--third>.modal-buy__photo  {
        transform: scale(1.2);
        border: solid 2px #D6936D;
        border-radius: 50%;
    }

    .ice-cream-types__ckeckbox--first:checked ~ .modal-buy__photo-container--first>.ice-cream__round  {
        background-color:  #96AF8C;
    }

    .ice-cream-types__ckeckbox--second:checked ~ .modal-buy__photo-container--second>.ice-cream__round  {
        background-color:  #EFD478;
    }

    .ice-cream-types__ckeckbox--third:checked ~ .modal-buy__photo-container--third>.ice-cream__round  {
        background-color: #D6936D;
    }

    .ice-cream-types {
        position: relative;
        display: flex;
        justify-content: center;
        flex-direction: column;
        list-style: none;
        padding: 0px;

        @media screen and (min-width:768px) {
        flex-direction: row;
        gap: 32px;
        }

    }

    .ice-cream-types__item {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 80px;

        &:last-child {
            margin-bottom: 100px;
        }

        @media screen and (min-width:768px) {
            margin-bottom: 100px;
            }

}

.ice-cream-types__item:first-child:hover  .ice-cream__round--first,
.ice-cream-types__item:first-child:focus  .ice-cream__round--first{
    background-color: #96AF8C;
}

.ice-cream-types__item:nth-child(2n):hover  .ice-cream__round--second,
.ice-cream-types__item:nth-child(2n):focus  .ice-cream__round--second {
    background-color: #EFD478;
}

.ice-cream-types__item:nth-child(3n):hover  .ice-cream__round--third,
.ice-cream-types__item:nth-child(3n):focus  .ice-cream__round--third {
    background-color: #D6936D;
}
    
    .ice-cream__round {
        display: flex;
        justify-content: center;
        align-items:flex-end;
        position: absolute;
        top: 60px;
        width: 132px;
        height: 136px;
        border-radius: 15px;
        z-index: -1;
}

.ice-cream__round--first {
    border: 2px solid #96AF8C;
    box-shadow: (0px 5.26027px 19.726px rgba(212, 20, 67, 0.1));
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.ice-cream__round--second {
    border: 2px solid #EFD478;
    box-shadow: (0px 5.26027px 19.726px rgba(212, 20, 67, 0.1));
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.ice-cream__round--third {
    border: 2px solid #D6936D;
    box-shadow: (0px 5.26027px 19.726px rgba(212, 20, 67, 0.1));
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.ice-cream__text {
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 16px;
    line-height: calc(20 / 16);
    text-transform: uppercase;
}

.ice-cream-types__ckeckbox {
    appearance: none;
    position: absolute;
    cursor: pointer;

    top: 0;
    left: 0;
    width: 132px;
    height: 197px;
    z-index: 5;


    // @include transitionRules (transform);
    
    // .ice-cream-types__ckeckbox:checked & {
    // transform: scale(1.1);
    // }
    
}

.form-buy {
    display: flex;
    flex-direction: column;
    margin: 0 auto;

    max-width: 368px;


    &__input {
        padding: 11px 8px;
        height: 32px;
    
        font-size: 12px;
        line-height: 1.4;
        margin-bottom: 12px;
    
        background-color: rgba(248, 210, 78, 0.1);
        border-width: 1px;
        border-style: solid;
        
        background: rgba(248, 210, 78, 0.1);
        border: 1px solid #84A178;
        border-radius: 10px;
        outline: none;
        
        &:last-child {
            margin-bottom: 0;
        }
    }

    &__input::placeholder {
        font-family: $main-font;
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 1.4;   
        color: #131313;
    }

    &__input--multiline {
    
        height: 64px;
        resize: none;
        margin-bottom: 32px;
    }
    &__input:valid:not(:placeholder-shown) {
        border-color: #84A178;
    }
    &__input:invalid:not(:placeholder-shown) {
        border-color: #ff0000;
    }
}